body {
  margin: 0;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  color: var(--color-text);
  background-color: #F5F4EF;
}
img {
  max-width: 100%;
}
h1 {
  font-weight: 600;
}
:root {
  --transition-duration: 250ms;
  --transition-function: ease-in-out;
  --color-text: #212121;
}
@font-face {
  font-family: 'Montserrat';
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/Montserrat-Regular.woff2) format('woff2'), url(../fonts/Montserrat-Regular.woff) format('woff');
}
@font-face {
  font-family: 'Montserrat';
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/Montserrat-SemiBold.woff2) format('woff2'), url(../fonts/Montserrat-SemiBold.woff) format('woff');
}
@font-face {
  font-family: 'Random Grotesque Standard';
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/RG-StandardSemibold.woff2) format('woff2'), url(../fonts/RG-StandardSemibold.woff) format('woff');
}
.nav__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.nav__link {
  position: relative;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: inherit;
  text-transform: lowercase;
  text-decoration: none;
  overflow: hidden;
  display: block;
}
.nav__link::after {
  content: attr(data-hover);
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(100%);
  transition: transform var(--transition-duration) var(--transition-function);
}
.nav__link span {
  transition: transform var(--transition-duration) var(--transition-function);
  display: block;
}
.nav__link:hover::after {
  transform: translateY(0);
}
.nav__link:hover span {
  transform: translateY(-100%);
}
.list-links {
  display: inline-flex;
  margin: 0;
}
.list-links a {
  display: block;
  margin-right: 8px;
  margin-bottom: 8px;
  font-weight: 600;
  color: inherit;
  text-transform: lowercase;
  text-decoration: none;
  border: 1px solid #212121;
  border-radius: 12px;
}
.portfolio__title {
  font-family: 'Random Grotesque Standard', Arial, Helvetica, sans-serif;
  font-weight: 600;
  font-size: 48px;
  line-height: 1;
  max-width: 660px;
}
